<template>
  <div id="sidebar">
    <avatar></avatar>
    <div class="icons">
      <router-link to="/note" class="link" title="笔记"><i class="iconfont icon-note"></i></router-link>
      <router-link to="/notebooks" class="link" title="笔记本"><i class="iconfont icon-notebook"></i></router-link>
      <router-link to="/trash" class="link" title="回收站"><i class="iconfont icon-trash"></i></router-link>
    </div>
    <div class="logout">
      <i class="iconfont icon-logout" @click="logout"></i>
    </div>
  </div>
</template>

<script>
  import avatar from './Avatar'
  import Auth from '../apis/auth'
  import  {Message} from 'element-ui'

  export default {
    components: {
      avatar
    },
    methods:{
      logout(){
        this.$confirm('是否要退出账号', '注销操作', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        .then(()=>{
          Auth.logout()
            .then((res)=>{
              this.$store.commit('setUser')
              this.$router.push('/login')
              Message.success(res.msg)
            })
        })
      }
    }
  }

</script>


<style lang="less" scoped>

  #sidebar {
    position: relative;
    width: 56px;
    text-align: center;
    background-color: #43433E;


    .icons {
      margin-top: 15px;


      a {
        padding: 6px 0;
        display: block;
      }
      .link{
        transition: all 0.04s ease-out;
      }
      .link:hover {
        transform: scale(1.5);
      }
      .router-link-active {
        background-color: #5e6266;
      }

    }


    .logout {
      position: absolute;
      bottom: 20px;
      width: 100%;
      text-align: center;
      cursor: pointer;
      transition: all 0.05s ease-out;
    }
    .logout:hover{
      transform: scale(1.5);
    }
    .iconfont {
      color: #fff;
    }
  }

</style>